import { BuildContext, Component, HtmlElement, InputComponent } from "@equantic/runtime";

export class Checkbox extends HtmlElement {
    render() {
        let inputAttrs = buildAttributes();inputAttrs["type"] = 'checkbox';if (this.value) inputAttrs["checked"] = 'true';if (this.disabled) inputAttrs["disabled"] = 'true';let events = buildEvents();return { tag: 'input', attributes: inputAttrs, events: events };
    }

}

