import { BuildContext, Component, ComponentState, getServerActionsClient, HtmlElement, InputComponent, StatefulComponent, StatelessComponent } from "@equantic/runtime";
import { Alert } from "./Alert";
import { Button } from "./Button";
import { Card } from "./Card";
import { Checkbox } from "./Checkbox";
import { Column } from "./Column";
import { Container } from "./Container";
import { Flex } from "./Flex";
import { Heading } from "./Heading";
import { Row } from "./Row";
import { Text } from "./Text";
import { TextInput } from "./TextInput";

export class TodoList extends StatefulComponent {
    createState() {
        return new TodoListState(this)
    }

    async getTodos() {
        return await getServerActionsClient().invoke('TodoList/GetTodos', [])
    }

    async addTodo(title: string) {
        return await getServerActionsClient().invoke('TodoList/AddTodo', [title])
    }

    async toggleTodo(id: string) {
        return await getServerActionsClient().invoke('TodoList/ToggleTodo', [id])
    }

    async deleteTodo(id: string) {
        return await getServerActionsClient().invoke('TodoList/DeleteTodo', [id])
    }

}


export class TodoListState extends ComponentState {
    _component: TodoList;
    _needsRender: boolean = false;
    _todos: Todo[] = [];
    _newTodoTitle: string = "";
    constructor(component: TodoList) {
        super();
        this._component = component;
    }

    setState(fn: () => void) {
        fn();
        this._needsRender = true;
        this._component._scheduleRender();
    }

    onInit(): void {loadTodos();}

    async loadTodos(): Promise<void> {this._todos = await this._component.getTodos();setState(() => {});}

    async handleAdd(): Promise<void> {if ((!this._newTodoTitle || this._newTodoTitle.trim() === '')) return;await this._component.addTodo(this._newTodoTitle);this._newTodoTitle = '';await loadTodos();}

    async handleToggle(id: string): Promise<void> {await this._component.toggleTodo(id);await loadTodos();}

    async handleDelete(id: string): Promise<void> {await this._component.deleteTodo(id);await loadTodos();}

    build(context: BuildContext) {
        let todoList = new Column({ gap: '5px' });for (const todo of this._todos) {todoList.children.push(new Row({ className: 'items-center p-2 border-b border-gray-100 dark:border-zinc-700', gap: '10px', children: [new Checkbox({ checked: todo.isCompleted, onChange: (_) => {handleToggle(todo.id);} }), new Text(todo.title, { className: todo.isCompleted ? 'line-through text-gray-400' : 'flex-1' }), new Button({ text: 'Delete', className: 'px-2 py-1 text-xs text-red-600 hover:bg-red-50 rounded', onClick: () => {handleDelete(todo.id);} })] }));}return new Container({ className: 'min-h-screen bg-gray-50 dark:bg-zinc-900 p-8 flex justify-center', children: [new Card({ width: 'w-full max-w-md', shadow: this.shadow.large, header: new Row({ className: 'items-center justify-between', children: [new Heading('Tasks', 2, { className: 'text-xl font-bold text-gray-800 dark:text-white' }), new Text(`${this._todos.count((t) => !t.isCompleted)} remaining`, { className: 'text-sm text-gray-500' })] }), body: new Column({ gap: '20px', children: [new Alert({ type: this.alertType.info, message: 'Welcome to eQuantic.UI with Tailwind!' }), new Row({ gap: '10px', children: [new TextInput({ value: this._newTodoTitle, placeholder: 'New task...', className: 'flex-1 px-3 py-2 border rounded border-gray-300 focus:ring-2 focus:ring-blue-500', onInput: (val) => setState(() => this._newTodoTitle = val) }), new Button({ text: 'Add', className: 'px-4 py-2 bg-blue-600 text-white rounded hover:bg-blue-700 font-medium', onClick: () => {handleAdd();} })] }), todoList] }) })] });
    }

}


